import React, { useState, useEffect } from 'react';
import { theme, isChrome } from '../../theme.js'
import { GrLinkedin } from "@react-icons/all-files/gr/GrLinkedin";
import { GrTwitter } from "@react-icons/all-files/gr/GrTwitter";
import Numbers from './numbers.js'
import "@fontsource/dm-sans";

const AboutUs = () => {
  
  const [isMobile, setIsMobile] = useState(false);

  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    setIsLoading(false)
    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);


  const containerStyle = {
    maxWidth: '950px',
    margin: '0 auto',
    padding: '20px',
    textAlign: 'center',
    // backgroundColor:'blue'
  };

  const titleStyle = {
    fontSize: '2rem',
    fontWeight: 'bold',
    // marginBottom: '140px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const foundersContainerStyle = {
    display: 'flex',
    justifyContent: 'center',
    gap: isMobile ? '20px' : '70px',
    // marginBottom: '10px',
    textAlign: 'left',
    flexWrap: isMobile ? 'wrap' : 'nowrap',
  };

  const founderStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  };

  const imageStyle = {
    width: '150px',
    height: '150px',
    borderRadius: '50%',
    marginBottom: '10px',
    backgroundColor:theme.colors.background,
    boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)',
    border: '1px solid #ccc',
  };


  const nameStyle = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    display: 'flex',
    alignItems: 'center',
  };

  const titleTextStyle = {
    fontSize: '1rem',
    fontFamily: theme.fonts.primary,
    color: theme.colors.primary,
    marginLeft: '8px',
  };

  const popupNameContainer = {
    display: 'flex',
    flexDirection: 'row', 
    alignItems: 'center', 
    justifyContent:'center', 
    marginTop:'10px'
  };

  const ulStyle = {
    listStylePosition: 'inside',
    padding: '0',
    // marginTop:'25px'
  };

  const liStyle = {
    marginBottom: '5px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '1rem',
    textAlign: 'left',
    paddingLeft: '1em', // Add padding to align text properly
    textIndent: isChrome() ? '-1.4em':'-1em', // Negative indent to pull the first line back
    marginLeft: '1em', // Adjust margin to properly align bullets
    // listStylePosition: 'inside', // Ensure bullets are inside the padding
    lineHeight: '1.5',

  };


  return (
    <div>
    {!isLoading && 
    <div style={containerStyle}>
      <p style={titleStyle}>{theme.aboutus.title}</p>
      <Numbers/>
      <p style={{ ...titleStyle, fontSize: '1.2rem', marginBottom:'50px' }}>Leadership Team</p>
      <div style={foundersContainerStyle}>
        {theme.aboutus.founders.map((founder) => (
          <div key={founder.id} style={founderStyle}>
            <img src={founder.image} alt={founder.name} style={imageStyle} />
            <div style={popupNameContainer}>
              <div style={nameStyle}>{founder.name}</div>
            </div>
            <div style={titleTextStyle}>{founder.title}</div>
            <div style={popupNameContainer}>
              <a href={founder.linkedin} target="_blank" rel="noopener noreferrer">
                    <GrLinkedin size={25} style={{margin:8, color:theme.colors.primary}}/>
                  </a>
                {founder.twitter &&
                  <a href={founder.twitter} target="_blank" rel="noopener noreferrer">
                    <GrTwitter size={25} style={{marginLeft:16, color:theme.colors.primary}}/>
                  </a>
                }
              </div>
            <ul style={ulStyle}>
              {founder.description.map((point, idx) => (
                <li key={idx} style={liStyle}>{point}</li>
              ))}
            </ul>
          </div>
        ))}
      </div>
    </div>
  }
  </div>
  );
};

export default AboutUs;
