import React, {useState,useEffect} from 'react';
import { IoLogoLinkedin } from "react-icons/io5";
import { MdEmail } from "react-icons/md";
import { IoLocation } from "react-icons/io5";
import { theme } from '../../theme.js'
import "@fontsource/dm-sans";

const Footer = () => {


   const [isMobile, setIsMobile] = useState(false);
   const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
      const handleResize = () => {
        setIsMobile(window.innerWidth <= 768);
      };
      setIsLoading(false)
      handleResize();
      window.addEventListener('resize', handleResize);

      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }, []);

  const footerStyle = {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#ffffff',
    margin:isMobile ? '0px':'30px',
  };

  const leftSectionStyle = {
     display: 'flex',
     flexDirection:'column',
     justifyContent: 'space-between',
  };

  const rightSectionStyle = {
    display: 'flex',
    flexDirection:'column',
    justifyContent: 'space-between',
  };

  const siteNameViewStyle = {
    display: 'flex',
    flexDirection: 'row',
  };

  const sitetitleStyle = {
    marginLeft: '8px',
    fontFamily:theme.fonts.primary,
    fontSize: isMobile ? '16px':'24px',
    fontWeight:'700',
    marginTop:'0px'
  };

  const textStyle = {
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    margin:0,
    marginLeft:'8px',
    marginTop:isMobile ? '-2px': '0px',
    fontSize: isMobile ? '0.85em':'1em' 
  };

  const lineStyle = {
    backgroundColor: '#D3D3D3',
    height: '1px',
    width: '100%',
    marginBottom:'10px'
  };


  const logoSize = isMobile ? 17:25

  return (
    <div>
    {!isLoading &&
    <footer>
      <div style={lineStyle}></div>
      <div style={footerStyle}>
        <div style={leftSectionStyle}>
          <div style={siteNameViewStyle}>
            <p style={{...sitetitleStyle, fontFamily:'Ubuntu'}}>\../</p>
            <p style={sitetitleStyle}>Stacqtrace</p>
          </div>
          <p style={{...textStyle, marginLeft:'8px'}}>{theme.footer.text}</p>
        </div>
        <div style={rightSectionStyle}>
          <div style={siteNameViewStyle}>
            <MdEmail size={logoSize}/>
            <a href={`mailto:contact@stacqtrace.com`} style={{ ...textStyle, marginBottom:'16px ', cursor: 'pointer', textDecoration: 'underline' }}>
              contact@stacqtrace.com
            </a>
          </div>
          <div style={siteNameViewStyle}>
            <IoLogoLinkedin size={logoSize}/>
            <a href={`https://www.linkedin.com/company/stacqtrace`} style={{ ...textStyle, marginBottom:'16px ', cursor: 'pointer', textDecoration: 'underline' }} target="_blank" rel="noopener noreferrer">
              LinkedIn
            </a>
          </div>
          <div style={siteNameViewStyle}>
            <IoLocation size={logoSize}/>
            <div>
              <p style={textStyle}>Delhi, India</p>
            </div>
          </div>
        </div>
      </div>
    </footer>
  }
  </div>
  );
};

export default Footer;
